@import url('https://fonts.googleapis.com/css?family=Lora');

html {
  height: 100%;
  box-sizing: border-box;
}

body {

  overflow: auto;
  position: relative;
}

body:before {
  content: "";
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;

  display: block;
  background-color: #fff;
  background-image: url("../img/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  width: 100%;
  height: 100%;

  filter: opacity(36%);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}

body {
  font-family: 'Lora', serif;
  position: relative;
  margin: 0;
  padding-bottom: 4rem;
  min-height: 100%;
}

a,
a:link,
a:visited,
a:active {
  color: #333;
  text-decoration: none;
}

a:hover {
  color: #555;
  text-decoration: none;
}

.cols {
  display: inline-block;
}

.center {
  margin: auto;
  text-align: center;
}

.social-list {
  list-style: none;
  padding: 0;
  li {
    display: inline-block;
    padding: 2px;
    margin: 3px
  }
}


#main-photo {

  height: 300px;
  padding: 20px;
  margin: 0;
  img {
    border-radius: 10px;
  }
}

#family-members {
  figure {
  vertical-align: top;
  }
}


header {
  margin: 0;
  padding: 35px;
}

h1,h2 {
  margin: 0;
  padding: 10px;
}

h1 {
  font-size: 2.5em;
}

h2 {
  font-size: 2em;
}

h3 {
  margin: 0;
  padding: 10px;
  font-size: 1.5em;
}



footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 14px;
  font-size: 0.765em;
}

.contact {
  p {
    font-size: .765em;
  }
}

#contact-form {
  text-align: center;
}

.contact-form-container {
  fieldset {
    border: 0;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  input {
    width: 280px;
    margin: 2px;
    font-size: .9em;
    border-radius: 3px;
    border: 1px black solid;
    padding: 2px;
  }

  textarea {
    width: 280px;
    height: 70px;
    margin: 2px;
    font-size: .9em;
    border-radius: 3px;
    border: 1px black solid;
    padding: 2px;
  }

  button {
    width: 280px;
    background-color: white;
    color: black;
    font-size: .9em;
    border: 1px black solid;
    border-radius: 3px;
    margin-bottom: 10px;
    padding: 2px;
  }

}
